@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0px;margin:0px;box-sizing:border-box}div,input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-tap-highlight-color:transparent}.App{display:flex;justify-content:center;align-items:center;background-color:#af98cc;height:100vh;font-family:"JetBrains Mono",sans-serif}.App .container{display:flex;justify-content:center;align-items:center;width:90vw}.App .container .grid{border:5px solid #000;width:100%;max-width:30rem;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(4, 1fr);grid-template-areas:"dis dis dis dis" "AC C divide multiply" "seven eight nine minus" "four five six plus" "one two three equal" "zero zero dot equal"}.App .container .grid .calcButton{display:flex;justify-content:center;align-items:center;padding:1.5rem 1.5rem;background-color:gray;color:#fff;border:1px solid #000;cursor:pointer}.App .container .grid .calcButton:active{background-color:#fff;color:#000}@media(hover: hover){.App .container .grid .calcButton:hover{background-color:#fff;color:#000}}@media(min-width: 700px){.App .container .grid .calcButton{padding:2rem 2rem}}.App .container .grid .dark-grey{background-color:#363636}.App .container .grid .tomato{background-color:tomato}.App .container .grid .blue{background-color:#4343f5}.App .container .grid .dis{grid-area:dis;background-color:#000;text-align:right}.App .container .grid .dis .input{border:none;width:100%;text-align:right;background-color:#000;color:orange;padding:2% 2%;font-family:"JetBrains Mono",sans-serif;font-size:1rem}.App .container .grid .dis .input:-ms-input-placeholder{color:orange}.App .container .grid .dis .input::placeholder{color:orange}.App .container .grid .dis .answer{padding:2% 2%;color:#fff}.App .container .grid .zero{grid-area:zero}.App .container .grid .equal{grid-area:equal}
